
@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro:400,300,600);

* {
  box-sizing: border-box;
}

body {
  font-size: 16px;
  font-family: 'Source Code Pro', monospace;
  text-transform: uppercase;
  margin: 0;
}

table td {
  transition: all 1s;
}

#color-sheet {
  position: relative;
  border: 1px solid white;
  font-size: 1.2em;
  width: 100%;
  height: 720px;
  text-align: center;
  vertical-align: middle;
  border-collapse: collapse;
}

input[type=range][orient=vertical]
{
    writing-mode: bt-lr; /* IE */
    -webkit-appearance: slider-vertical; /* WebKit */
    width: 8px;
    height: 180px;
    padding: 0 5px;
}

.color-input {
  padding: 3em;
  &:before {
    content: "#";
  }
  input {
    font-family: 'Source Code Pro', monospace;
    font-size: 1em;
    padding: 0.3em 0em 0.25em 0.25em;
    background: transparent;
    border: none;
    border-bottom: 2px solid rgba(255, 255, 255, 0.5);
    text-transform: uppercase;
    &:focus {
      outline: none;
      background: rgba(0, 0, 0, 0.15);
      border-color: white;
    }
  }
}
